import React, { useEffect } from "react";
import AddShiftForm from "../components/AddShiftForm";
import { connect } from "react-redux";

import { useSnackbar } from "notistack";
import { addnewShift } from "../store/actions/ShiftActions";
import { getallParttime } from "../store/actions/CompanyActions";
const AddShift = (props) => {

  const { enqueueSnackbar } = useSnackbar();
  useEffect(() => {
    const data = {
      userId: props?.userData?.loginData?.user_id,
    };
    props.getallParttime(data);
  }, []);

  const handleShiftAdd = async (params) => {
    if (params != null) {
      try {
        const data = {
          ...params,
          user_id: props?.userData?.loginData?.user_id,
        };
        //console.log(data,props?.userData?.loginData)
        const res = await props.addnewShift(data);
        enqueueSnackbar("Shift added Successful", { variant: "success" });
      } catch (err) {
        console.log(err);
        enqueueSnackbar(err, { variant: "error" });
      }
    }
  };

  return (
    props?.companyData?.isparttimeListFetched && (
      <AddShiftForm
        handleShiftAdd={handleShiftAdd}
        companyData={props?.companyData}
      />
    )
  );
};

function mapStateToProps(state) {
  return {
    userData: state.LoginReducer,
    companyData: state.CompanyReducer,
    ShiftData: state.ShiftReducer,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    addnewShift: (params) => dispatch(addnewShift(params)),
    getallParttime: (params) => dispatch(getallParttime(params)),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(AddShift);
